@if map-get($vars, enabled) {
	#{$prefix}.checkbox {
		@extend %lui_input_holder_base;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		line-height: 1;

		input, label { margin: 0; }

		input {
			position: absolute;
			left: 0; top: 0;
			z-index: 2;
			width: 100%;
			height: 100%;
			cursor: pointer;
			opacity: 0;
		}

		label {
			color: map-gets($vars, borderColor);
			display: block;
			position: relative;
			padding: 0;

			@include lui_make_icon("checkbox");

			&:before,&:after { display: block; font-size:  map-gets($vars, checkbox, size); }
		}

		input:checked ~ label {
			color: map-gets($vars, checkbox, color);
			@include lui_make_icon("checkbox checked");
		}
	}

	// Icon version
	// ====
	#{$prefix}.checkbox.icon {
		&[class*="checkbox checked"]:before { color: map-gets($vars, checkbox, color); }

		&:before { font-size: map-gets($vars, checkbox, size); color: map-gets($vars, borderColor); }
	}
	#{$prefix}.checkbox.icon.button {
		padding: 0.25em;
	}
}
